.video-detalle {
    height: 250px;
    margin-top: 40px;
    margin-bottom: 40px;
}

.portada-video-detalle:hover {
    -webkit-filter: brightness(0.75);
            filter: brightness(0.75);
    z-index: 2;
}

.portada-video-detalle {
    position: relative;
    cursor: pointer;
}

.portada-video-detalle img {
    position: absolute;
    width: 250px;
    height: 250px;
    z-index: 2;
}

.portada-video-detalle span {
    position: absolute;
    margin-top: 89px;
    margin-left: 93px;
    z-index: 2;
}

.detalle-video-detalle {
    position: relative;
    transform: scaleX(-1);
    opacity: 0.75;
    background: linear-gradient(0deg, rgba(167, 0, 0, 0.5), rgba(167, 0, 0, 1)),
        url("https://wallpaperaccess.com/full/1695845.jpg") no-repeat;
    background-size: cover;
    width: 890px;
    height: 250px;
}

.detalle-video-detalle .contenido {
    position: absolute;
    transform: scaleX(-1);
    margin-top: 40px;
    margin-right: 290px;
    margin-left: 55px;
    z-index: 1;
    height: 250px;
}

.seguidores {
    font-size: 13px;
    color: #240c0c;
}

.reproducir {
    border: 2px solid var(--principal);
    background-color: var(--principal);
    color: white;
    border-radius: 50px;
    width: 117px;
}

.seguir {
    background-color: transparent;
    color: var(--principal);
    border: 1px solid var(--principal);
    border-radius: 50px;

    width: 117px;
}

.mas {
    background-color: transparent;
    color: white;
    border: 0px;
    font-weight: bold;
    font-size: 40px;
    text-align: top;
}

.detalle {
    margin-top: 20px;
}

.resultados{
    padding-bottom: 200px;
}


.wrapper{
  position: fixed;
  z-index: 1;
}

.wrapper-button{
  position: relative;
}

#sidebar{
  width: 330px;
  height: 100vh;
  background-color: var(--dark-secodary);
}

#sidebar .sidebar-header {
  padding-top: 10px;
}

.logo{
  width: 250px;
  height: 59px;
  margin: 40px 40px 0px 40px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.wrapper ul p{
  padding-left: 40px;
  margin-top: 30px !important;
  margin-bottom: 4px !important;
}

#sidebar ul li a{
  padding-left: 40px;
  text-decoration:none;
  color: #ffffff;
  font-weight: 400;
}

.wrapper li{
  margin-bottom: 8px !important;
}

.wrapper .active a{
  text-decoration:none;
  color: var(--principal) !important;
  padding-left: 35px !important;
  border-left: 5px solid var(--principal);
}

#sidebar ul li a:hover{
  text-decoration:none;
  font-weight: bold;
}



.wrapper-button span{
  position: absolute;
  top: 34px;
  width: 200px;
  height: 100px;
  color: rgba(189,189,189,1);
}

#icon-barra-mostrar{
  cursor: pointer;
}
.barra-control{
    height: 100px;
    width: 100%;
    z-index: 2;
    background: #EB5757;
    box-shadow: inset 0px -5px 37px rgba(0, 0, 0, 0.2);
    -webkit-filter: drop-shadow(0px -5px 37px rgba(0, 0, 0, 0.2));
            filter: drop-shadow(0px -5px 37px rgba(0, 0, 0, 0.2));
}
