    }
}
.acciones span{
    cursor: pointer;
}

.play{
    width: 60px;
    height: 60px;
    padding: 18px 23px;
    margin: 0px 20px 0px 20px;
    background-color: var(--light-red);
    border-radius: 100%;
}

@media only screen and (max-width: 576px) {
    .steps{
